import React, {useState,useContext, useEffect} from 'react';
import axios from 'axios';
import {AuthContext} from '../context/AuthContext';

const Dashboard = function(){
    
    let {setLoggedin,setToken,token} = useContext(AuthContext);

    const [loading, setLoading] = useState(true);
    const [requesting, setRequesting] = useState(false);
    const [message, setMessage] = useState('No Message');
    const [counter, setCounter] = useState('300');

    useEffect(() => {
        setLoading(true);
        
        var storageToken = window.localStorage.getItem('token');
        if(storageToken){
            var token = JSON.parse(storageToken);

            // eslint-disable-next-line react-hooks/exhaustive-deps
            setLoggedin(true);
            setToken(token);
        }

        setLoading(false);

    },[])

    const handleCounterChange = function(e){
        setCounter(e.target.value);
    }
    const handleCurrentCounter = function(){
        if (!requesting){
            setRequesting (true);
            var base = (process.env.NODE_ENV==='development')
            ? process.env.REACT_APP_API_URL
            : window.location.origin

            axios({
                method: "get",
                url: base+"/v1/current",
                headers:{
                    "Authorization":`Bearer ${token}`
                }
            })
            .then(function(res){
                
                setMessage(`Current Counter Value is => ${res.data.message}`)
                setRequesting (false);

            }).catch(function(err){
                setRequesting (false);
                if (typeof(err.response)=="undefined"){
                    setMessage(`error => Request timeout!`);
                }else{
                    setMessage(`error =>  ${err.response.data.message}`);
                }
            });

        }else{
            setMessage('Another Request in Progress! wait for that one to finish!');
        }
        
    }
    const handleNextCounter = function(){
        if (!requesting){
            setRequesting (true);
            var base = (process.env.NODE_ENV==='development')
            ? process.env.REACT_APP_API_URL
            : window.location.origin

            axios({
                method: "get",
                url: base+"/v1/next",
                headers:{
                    "Authorization":`Bearer ${token}`
                }
            })
            .then(function(res){
                
                setMessage(`Next Counter Value is => ${res.data.message}`)
                setRequesting (false);

            }).catch(function(err){
                setRequesting (false);
                if (typeof(err.response)=="undefined"){
                    setMessage(`error => Request timeout!`);
                }else{
                    setMessage(`error =>  ${err.response.data.message}`);
                }
            });

        }else{
            setMessage('Another Request in Progress! wait for that one to finish!');
        }
    }
    const handleUpdateCounter = function(){
        if (!requesting){
            if (counterValid()){
                setRequesting (true);
                var base = (process.env.NODE_ENV==='development')
                ? process.env.REACT_APP_API_URL
                : window.location.origin

                axios({
                    method: "put",
                    url: base+"/v1/current",
                    headers:{
                        "Authorization":`Bearer ${token}`
                    },
                    data:{"current":counter}
                })
                .then(function(res){
                    
                    setMessage(`Current Counter Value is => ${counter}`)
                    setRequesting (false);

                }).catch(function(err){
                    setRequesting (false);
                    if (typeof(err.response)=="undefined"){
                        setMessage(`error => Request timeout!`);
                    }else{
                        setMessage(`error =>  ${err.response.data.message}`);
                    }
                });
            }
        }else{
            setMessage('Another Request in Progress! wait for that one to finish!');
        }
    }

    const counterValid = function(){
        if (
            (counter.length>0) && (!isNaN(counter))){
            return true;
        }else{
            setMessage('Counter value is not a Number');
            return false;
        }
    }

    const handleLogout = function(){
        deleteToken();
        setLoggedin(false);
        setToken('');
    }

    const deleteToken = function(){
        window.localStorage.removeItem('token');
    }
    return (
        loading
        ?'...Loading...'
        :(<>
            <div>{message}</div>
            <button
            onClick = {()=>handleCurrentCounter()}
            >Current Counter</button>
            
            <button
            onClick = {()=>handleNextCounter()}
            >Next Counter</button>

            <input 
            type="text"
            placeholder="counter"
            onChange={(e)=>handleCounterChange(e)}
            value={counter}/>
            
            <button
            onClick = {()=>handleUpdateCounter()}
            >Update Counter Value</button>

            <button
            onClick = {()=>handleLogout()}
            >Logout</button>
        </>)
    )
}

export default Dashboard;