import React, {useState,useContext, useEffect} from 'react';
import axios from 'axios';
import { useHistory } from "react-router-dom";
import {AuthContext} from '../context/AuthContext';

const Login = function(){

    // useEffect
    let {setLoggedin,setToken} = useContext(AuthContext);
    let history = useHistory();

    const [loading, setLoading] = useState(true);
    const [requesting, setRequesting] = useState(false)
    const [error, setError] = useState('No Error');
    const [formData,setFormData]=useState({
        email: '',
        password: '',
    });

    useEffect(() => {
        setLoading(true);
        
        var storageToken = window.localStorage.getItem('token');
        if(storageToken){
            var token = JSON.parse(storageToken);
            setLoggedin(true);
            setToken(token);
            history.push('/dashboard')
        }

        setLoading(false);

    }, [])

    const handleFormChange = function(fieldName){
        return function (e){
            setFormData({ 
                ...formData, 
                [fieldName]: e.target.value 
            });
        }
    }
    const handleGotoRegistration = function(){
        history.push('/register');
    }

    const handleLoginButton = function(){ 
        if (!requesting){
            if (formDataValid()){
                
                setRequesting (true);
                var base = (process.env.NODE_ENV==='development')
                ? process.env.REACT_APP_API_URL
                : window.location.origin

                axios({
                    method: "post",
                    url: base+"/v1/loginUser",
                    data:formData
                })
                .then(function(res){
                    setFormData({
                        email:'',
                        password:''
                    })
                    setError(`success Congrats! Logged in Successfully`)
                    setRequesting (false);
                    setLoggedin(true);
                    const token = res.data.message;
                    setToken(token);
                    saveToken(token);
                    history.push('/dashboard')

                }).catch(function(err){
                    setRequesting (false);
                    if (typeof(err.response)=="undefined"){
                        setError(`error => Request timeout!`);
                    }else{
                        setError(`error =>  ${err.response.data.message}`);
                    }
                });
            }
        }else{

            setError('Another Request in Progress! wait for that one to finish!');
        }
    }
    
    const formDataValid = function(){
        if (emailValid() && passwordValid()){
            setFormData({
                email:formData.email.trim(),
                password:formData.password.trim(),
            })
            return true;
        }
        return false;
    }

    const passwordValid = function(){
        if (formData.password.length>0){
                // console.log(formData.password.length)
                return true;
        }
        setError(`PasswordNotValid`)
        return false;
    }
    const emailValid = function(){
        if (formData.email.length>0){
            // console.log(formData.email.length)
            if (formData.email.indexOf('@')>-1){
                // console.log(formData.email.indexOf('@'))
                if((formData.email.split('@')[1]).indexOf('.')>-1){
                    return true;
                }
            }
        }
        setError(`Email is not valid!`)
        return false;
    }

    const saveToken = function(token){
        window.localStorage.setItem('token',JSON.stringify(token));
    }
    
    return (
        loading
        ?'....loading'
        :
        (
        <>
            <div>{error}</div>
            <input 
            type="email"
            placeholder="email"
            onChange={handleFormChange('email')}
            value={formData.email}/>

            <input
            type="text"
            placeholder="Password"
            onChange={handleFormChange('password')}
            value={formData.password}/>
            
            <button
            onClick = {()=>handleLoginButton()}
            >Login</button>

            <button
            onClick = {()=>handleGotoRegistration()}
            >Goto Registration</button>
        </>
        ))
}

export default Login;